import { useTheme } from "../../hooks/useTheme"; // ðŸ”¹ AsegÃºrate de importar el hook correctamente
import Image from "next/image";

const CertificateCard = ({ 
  title, 
  subtitle, 
  status, 
  image, 
  lockedImage, 
  description_locked, // ðŸ”¹ Corregido: sin espacios
  description_unlocked // ðŸ”¹ Corregido: sin espacios
}) => {
  const theme = useTheme(); // ðŸ”¹ Obtiene el tema actual

  // ðŸ”¹ Cambia el icono segÃºn el tema
  const lockIcon = theme === "dark" 
    ? "/assets/dark/lock.svg" 
    : "/assets/light/lock.svg";  

  return (
    <div className={`p-6 border rounded-lg shadow-md ${status === "locked" ? "opacity-50" : "opacity-100"}`}>
      <h2 className="text-lg font-bold">{title}</h2>
      <p className="text-sm text-gray-500">{subtitle}</p>

      {/* ðŸ”¹ Muestra el candado si estÃ¡ bloqueado, o la imagen si estÃ¡ desbloqueado */}
      <Image
        src={status === "locked" ? lockIcon : image}
        alt={status === "locked" ? "Certificado bloqueado" : "Certificado"}
        width={200}
        height={120}
        className="mx-auto my-4"
      />

      {/* ðŸ”¹ DescripciÃ³n segÃºn el estado */}
      <p className="text-sm text-gray-600">
        {status === "locked" ? description_locked : description_unlocked}
      </p>

      {/* ðŸ”¹ BotÃ³n solo si estÃ¡ desbloqueado */}
      {status === "completed" && (
        <button className="mt-4 bg-primary text-white px-4 py-2 rounded-lg">
          Descargar Certificado
        </button>
      )}
    </div>
  );
};

export default CertificateCard;
